body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border: 1px solid #f9e7e7;
    box-shadow: 0 5px 10px rgba(0,0,0,.5);
    padding: 10px 10px 40px 10px;
    -webkit-animation: loginForm .2s linear;
            animation: loginForm .2s linear;
}

.login {
    padding: 14px;
    color: white;
    background-image: lightgray;
    background-image: linear-gradient(to bottom right,#007bff,#3ee865);
}

.reset-password {
    padding: 14px;
    color: white;
    background-image: lightgray;
    background-image: linear-gradient(to bottom right,#ff4200,#b3d7ff);
}

@-webkit-keyframes loginForm {
    0% {
        opacity: 0;
        top:10%;
        -webkit-transform: scale3d(0.1,0.1,0.1) translate(-50%,-50%);
                transform: scale3d(0.1,0.1,0.1) translate(-50%,-50%);
    }

    30% {
        opacity: .2;
        top:20%;
        -webkit-transform: scale3d(0.2,0.2,0.2) translate(-50%,-50%);
                transform: scale3d(0.2,0.2,0.2) translate(-50%,-50%);
    }

    60% {
        opacity: .4;
        top:30%;
        -webkit-transform: scale3d(0.4,0.4,0.4) translate(-50%,-50%);
                transform: scale3d(0.4,0.4,0.4) translate(-50%,-50%);
    }

    80% {
        opacity: .8;
        top:40%;
        -webkit-transform: scale3d(0.5,0.5,0.5) translate(-50%,-50%);
                transform: scale3d(0.5,0.5,0.5) translate(-50%,-50%);
    }

    100% {
        opacity: 1;
    }
}

@keyframes loginForm {
    0% {
        opacity: 0;
        top:10%;
        -webkit-transform: scale3d(0.1,0.1,0.1) translate(-50%,-50%);
                transform: scale3d(0.1,0.1,0.1) translate(-50%,-50%);
    }

    30% {
        opacity: .2;
        top:20%;
        -webkit-transform: scale3d(0.2,0.2,0.2) translate(-50%,-50%);
                transform: scale3d(0.2,0.2,0.2) translate(-50%,-50%);
    }

    60% {
        opacity: .4;
        top:30%;
        -webkit-transform: scale3d(0.4,0.4,0.4) translate(-50%,-50%);
                transform: scale3d(0.4,0.4,0.4) translate(-50%,-50%);
    }

    80% {
        opacity: .8;
        top:40%;
        -webkit-transform: scale3d(0.5,0.5,0.5) translate(-50%,-50%);
                transform: scale3d(0.5,0.5,0.5) translate(-50%,-50%);
    }

    100% {
        opacity: 1;
    }
}

.loading-icon{
    width: 100px;
    position: absolute;
    top: 23%;
    left: 49%;
    z-index: 1000;
}
* {
  font-family:Arial, Helvetica, sans-serif;

}
.clear-float::after {
  content:"";
  display:inherit;
  clear: both;

}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

